
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'

import {mapGetters, mapMutations} from 'vuex'
import {adver} from '../config'
export default {
  head: {
    title: {
      inner: '积分商城'
    }
  },
  data () {
    return {
      score: 0,
      userId: 'null',
      list: [],
      recommend: [],
      advert: [],
      product: {
        type: 0,
        timeOrder: 0,
        priceOrder: 0,
        defaultOrder: 0,
        limit: 8,
        pageIndex: 0
      }
    }
  },
  created () {
    this.getProduct(this)
    if (this.$localStorage.get('logined') === 'true') {
      this.score = JSON.parse(this.$localStorage.get('userInfo')).userBalance
      this.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
    }
  },
  computed: {
    ...mapGetters({
      nav: 'getMallNav'
    })
  },
  methods: {
    getrecommend () {
      this.$http({
        method: 'jsonp',
        url: adver,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: {
          position: 1
        }
      })
      .then(res => {
        console.log(res)
      })
    },
    ...mapMutations({
      getProduct: 'getProduct'
    }),
    handleSaveData (item) {
      console.log(item)
      this.$localStorage.set('goods', JSON.stringify(item))
    }
  },
  components: {
    Swiper,
    SwiperItem,
    XImg
  }
}



